//Components

import { useTranslation } from "react-i18next";

export default function VideoTibs({ sellerdetails }) {
  const { t } = useTranslation();
    const youtubeUrl = (url) => {
    var urlsplit = url.split(
      /^.*(youtu.be\/|v\/|embed\/|watch\?|youtube.com\/user\/[^#]*#([^\/]*?\/)*)\??v?=?([^#\&\?]*).*/
    );
    const youtubeUrl = "https://www.youtube.com/embed/" + urlsplit[3];
    return youtubeUrl;
  };
  return (
    <>
      <h3 className="mb-8 font-bold text-black-300 text-2xl">
        {t("seller.videoTips")}
      </h3>
      <div className="gap-4 gap-y-4 sm:gap-y-8 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4">
        {sellerdetails?.vendorVideos?.map((data, index) => (
          <a
            role="button"
            key={index}
            className="group flex flex-col space-y-3 cursor-pointer"
          >
            <div className="relative">
              <iframe
                src={youtubeUrl(data.url)}
                title="description"
                className="w-full"
                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; fullscreen"
                allowFullScreen
              ></iframe>
            </div>
            <p className="font-semibold text-black-300 group-hover:text-green-600 text-sm line-clamp-2">
              {data.title}
            </p>
          </a>
        ))}
      </div>
    </>
  );
}
