"use client";
import { useTranslation } from "react-i18next";
//React and Next
import { useEffect, useRef, useState } from "react";
//Components
import ProfileTabs from "./Profile";
import CertificationTab from "./Certification";
import VideoTibs from "./video";
import ProductSeller from "./Product";


export default function SellerTabs({ token, sellerproduct, sellerdetails }) {
  const { t } = useTranslation();
  const [activeLink, setActiveLink] = useState("");

  
  const profile = useRef(null);
  const certifications = useRef(null);
  const videoTips = useRef(null);
  const products = useRef(null);

  const scrollToSection = (current) => {
    setActiveLink(current);
    if (current === "#tab1") {
      const yOffset = -100;
      const y =
        profile.current.getBoundingClientRect().top +
        window.pageYOffset +
        yOffset;
      window.scrollTo({ top: y, behavior: "smooth" });
    }
    if (current === "#tab2") {
      const yOffset = -100;
      const y =
        certifications.current.getBoundingClientRect().top +
        window.pageYOffset +
        yOffset;
      window.scrollTo({ top: y, behavior: "smooth" });
    }
    if (current === "#tab3") {
      const yOffset = -100;
      const y =
        videoTips.current.getBoundingClientRect().top +
        window.pageYOffset +
        yOffset;
      window.scrollTo({ top: y, behavior: "smooth" });
    }
    if (current === "#tab4") {
      const yOffset = -100;
      const y =
        products.current.getBoundingClientRect().top +
        window.pageYOffset +
        yOffset;
      window.scrollTo({ top: y, behavior: "smooth" });
    }
  };

  const sectionMap = {
    "#tab1": profile,
    "#tab2": certifications,
    "#tab3": videoTips,
    "#tab4": products,
  };

  useEffect(() => {
    const handleScroll = () => {
      const scrollPos = window.scrollY;

      for (const [selector, ref] of Object.entries(sectionMap)) {
        const section = ref.current;
        if (section) {
          const elementTop = section.offsetTop;
          const elementHeight = section.offsetHeight;
          if (
            elementTop - 100 <= scrollPos &&
            elementTop + elementHeight >= scrollPos
          ) {
            setActiveLink(selector);
          }
        }
      }
    };

    // Attach scroll event listener
    window.addEventListener("scroll", handleScroll);

    // Cleanup event listener on component unmount
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const certificateLenth = sellerdetails?.certificates.filter(
    (certificates) => certificates.additionalInfo !== null
  );

  return (
    <div>
      <div className="top-[4.5rem] md:top-20 z-20 sticky flex items-center space-x-12 bg-white shadow-sm px-0 sm:px-10 pt-[31px] pb-0 sm:pb-4 overflow-auto whitespace-nowrap cursor-pointer">
        <a
          role="button"
          className={
            activeLink == "#tab1"
              ? "border-b-2 border-green-900 text-activegreen rtl:ml-12"
              : "text-black-300 text-base font-medium leading-6 hover:text-green-600 rtl:ml-12"
          }
          onClick={() => scrollToSection("#tab1")}
        >
          {t("seller.profile")}
        </a>
        <a
          role="button"
          className={
            activeLink == "#tab2"
              ? "border-b-2 border-green-900 text-activegreen"
              : "text-black-300 text-base font-medium leading-6 hover:text-green-600"
          }
          onClick={() => scrollToSection("#tab2")}
        >
          {t("seller.certifications")} ({certificateLenth?.length})
        </a>
        <a
          role="button"
          className={
            activeLink == "#tab3"
              ? "border-b-2 border-green-900 text-activegreen"
              : "text-black-300 text-base font-medium leading-6 hover:text-green-600"
          }
          onClick={() => scrollToSection("#tab3")}
        >
          {t("seller.videoTips")} ({sellerdetails?.vendorVideos?.length})
        </a>
        <a
          role="button"
          className={
            activeLink == "#tab4"
              ? "border-b-2 border-green-900 text-activegreen"
              : " text-black-300 text-base font-medium leading-6 hover:text-green-600"
          }
          onClick={() => scrollToSection("#tab4")}
        >
          {t("seller.products")} ({sellerproduct?.length})
        </a>
      </div>
      <div className="px-0 py-5 sm:p-10">
        <div
          ref={profile}
          id="tab1"
          className="pb-12 mb-12 border-b border-grey"
        >
          <ProfileTabs sellerdetails={sellerdetails} />
        </div>
        <div
          ref={certifications}
          id="tab2"
          className="pb-12 mb-12 border-b border-grey"
        >
          <CertificationTab token={token} sellerdetails={sellerdetails} />
        </div>
        <div
          ref={videoTips}
          id="tab3"
          className="pb-12 mb-12 border-b border-grey"
        >
          <VideoTibs sellerdetails={sellerdetails} />
        </div>
        <div ref={products} id="tab4" className="mb-12">
          <ProductSeller token={token} sellerproduct={sellerproduct} />
        </div>
      </div>
    </div>
  );
}
