"use client";
//Api
import { imageUrl } from "@/utilities/url";
//Component
import ConnectPlugin from "@/Components/connectPlugins";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

export default function SellerBanner({ sellerdetails, addon, handleContactSeller }) {
  const { t } = useTranslation();
    const [langCode, setLangCode] = useState(null);

  useEffect(() => {
    const code = localStorage.getItem("languageCode");
    setLangCode(code);
  }, []);

  const convertLink = (link) => {
    if (!/^https?:\/\//i.test(link)) {
      link = "http://" + link;
      return link;
    } else {
      return link;
    }
  };

  return (
    <>
      <div className="w-full h-[461px]">
        <img
          src={
            imageUrl +
            "?path=" +
            sellerdetails?.companyCoverImagePath +
            "&name=" +
            sellerdetails?.companyCoverImage +
            "&width=2000&height=500"
          }
          alt={sellerdetails?.companyName}
          onError={({ currentTarget }) => {
            currentTarget.onerror = null;
            currentTarget.src = "/img/no-image.png";
            currentTarget.alt = "no-image";
          }}
          className="w-full h-full"
        />
      </div>
      <div className="bg-lightblue">
        <div className="px-4 py-6 mx-auto sm:px-10 max-w-screen-2xl">
          <div className="flex flex-col justify-between space-x-4 sm:flex-row">
            <div className="flex items-center space-x-4 rtl:space-x-[unset]">
              <div className="rtl:ml-4 w-full sm:w-[120px] h-full sm:h-[120px]">
                <img
                  src={
                    imageUrl +
                    "?path=" +
                    sellerdetails?.companyLogoPath +
                    "&name=" +
                    sellerdetails?.companyLogo +
                    "&width=200&height=200"
                  }
                  alt={sellerdetails?.companyName}
                  onError={({ currentTarget }) => {
                    currentTarget.onerror = null;
                    currentTarget.src = "/img/no-image.png";
                    currentTarget.alt = "no-image";
                  }}
                />
              </div>
              <div className="flex flex-col space-y-[5px]">
                <h4 className="font-medium text-[32px] text-black-300">
                  {sellerdetails?.companyName} {""}
                </h4>
                <p className="font-normal text-[13px] text-black-300 leading-4">
                  {sellerdetails?.companyAddress1 &&
                    `${sellerdetails?.companyAddress1},`}
                  {sellerdetails?.companyAddress2 &&
                    `${sellerdetails?.companyAddress2},`}{" "}
                  {sellerdetails?.countryName}
                </p>
                <div className="flex items-center mt-px space-x-4">
                  <span className="rtl:ml-4 font-normal text-[13px] text-black-300 leading-4">
                    {sellerdetails?.totalOrders} {t("seller.sales")}
                  </span>
                  <div className="flex items-center gap-0.5">
                    {addon?.["rating-review"]?.status == 1 &&
                      ConnectPlugin.VendorRating && (
                        <ConnectPlugin.VendorRating
                          vendorId={sellerdetails?.vendorId}
                        />
                      )}
                  </div>
                </div>
              </div>
            </div>
            <div className="flex flex-col-reverse items-center space-y-8 lg:flex-row lg:items-start lg:space-x-8 lg:space-y-0">
              <div className="flex items-center space-x-3">
                {sellerdetails?.facebook !== "" &&
                  sellerdetails?.facebook !== null &&
                  sellerdetails?.facebook !== undefined && (
                    <a
                      href={convertLink(sellerdetails?.facebook)}
                      hrefLang={langCode}
                      target="_blank"
                      className="flex justify-center items-center bg-[#EAEAF5] hover:bg-slate-200 px-1.5 rounded-full w-8 h-8"
                    >
                      <img
                        src="/img/seller-contact4.svg"
                        alt="seller-contact4"
                        className="w-5"
                      />
                    </a>
                  )}
                {sellerdetails?.instagram !== "" &&
                  sellerdetails?.instagram !== null &&
                  sellerdetails?.instagram !== undefined && (
                    <a
                      href={convertLink(sellerdetails?.instagram)}
                      hrefLang={langCode}
                      target="_blank"
                      className="flex justify-center items-center bg-[#EAEAF5] hover:bg-slate-200 px-1.5 rounded-full w-8 h-8"
                    >
                      <img
                        src="/img/seller-contact3.svg"
                        alt="seller-contact3"
                        className="w-5"
                      />
                    </a>
                  )}
                {sellerdetails?.youtube !== "" &&
                  sellerdetails?.youtube !== null &&
                  sellerdetails?.youtube !== undefined && (
                    <a
                      href={convertLink(sellerdetails?.youtube)}
                      hrefLang={langCode}
                      target="_blank"
                      className="flex justify-center items-center bg-[#EAEAF5] hover:bg-slate-200 px-1.5 rounded-full w-8 h-8"
                    >
                      <img
                        src="/img/seller-contact2.svg"
                        alt="seller-contact2"
                        className="w-5"
                      />
                    </a>
                  )}
                {sellerdetails?.whatsapp !== "" &&
                  sellerdetails?.whatsapp !== null &&
                  sellerdetails?.whatsapp !== undefined && (
                    <a
                      href={convertLink(sellerdetails?.whatsapp)}
                      hrefLang={langCode}
                      target="_blank"
                      className="flex justify-center items-center bg-[#EAEAF5] hover:bg-slate-200 px-1.5 rounded-full w-8 h-8"
                    >
                      <img
                        src="/img/seller-contact1.svg"
                        alt="seller-contact1"
                        className="w-5"
                      />
                    </a>
                  )}
              </div>

              <div className="flex flex-col items-center space-y-[5px]">
                <div className="w-16 h-16 rounded-full min-w-16">
                  <img
                    src={
                      imageUrl +
                      "?path=" +
                      sellerdetails?.persnolInfo?.avatarPath +
                      "&name=" +
                      sellerdetails?.persnolInfo?.avatar +
                      "&width=200&height=200"
                    }
                    alt={`${sellerdetails?.persnolInfo?.firstName}  ${sellerdetails?.persnolInfo?.lastName}`}
                    onError={({ currentTarget }) => {
                      currentTarget.onerror = null;
                      currentTarget.src = "/img/no-image.png";
                      currentTarget.alt = "no-image";
                    }}
                    className="object-contain w-full h-full rounded-full"
                  />
                </div>
                <p className="text-sm font-normal text-black-300">
                  {sellerdetails?.persnolInfo?.firstName}{" "}
                  {sellerdetails?.persnolInfo?.lastName}
                </p>
                <a
                  role="button"
                  onClick={handleContactSeller}
                  className="flex items-center space-x-1 rtl:space-x-[unset] bg-lightgray hover:bg-slate-200 px-4 rounded-[33px] h-[31px] font-semibold text-black-300 text-xs cursor-pointer"
                >
                  <img
                    src="/img/msg-icon.svg"
                    alt="msg-icon"
                    className="rtl:ml-1"
                  />
                  <p>{t("myprofile.Contact")}</p>
                </a>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
