"use client";
// React & Next Imports
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
//Api
import { imageUrl } from "@/utilities/url";
//Component
import CustomCurrencyFormatter from "@/Components/CustomCurrencyFormatter";

export default function SellerPerformace({ sellerdetails }) {
  const { t } = useTranslation();
  const [imageSlide, setImageSlide] = useState([]);
    const [show, setShow] = useState(false);
  const handLandleImageLoaded = (listdata) => {
    let sdata = listdata?.vendorImages?.find((s) => s.defaultImage == 1);
    if (sdata) {
      setImageSlide(sdata);
    }
  };
  useEffect(() => {
    handLandleImageLoaded(sellerdetails);
  }, []);

  const hadleChageImgeLoad = (data) => {
    {
      sellerdetails?.vendorImages?.map((sdata) => {
        if (sdata.id == data.id) {
          sdata.defaultImage = 1;
        } else {
          sdata.defaultImage = 0;
        }
      });
    }
    setImageSlide(data);
  };
  return (
    <div className="grid items-start grid-cols-1 gap-10 mb-20 md:grid-cols-2">
      <div>
        <div className="pb-8 mb-8 border-b border-grey">
          <h3 className="text-xl font-bold leading-6 mb-7 text-black-300">
            {t("seller.performance")}
          </h3>
          <div className="flex items-center space-x-2 rtl:space-x-[unset] mb-2">
            <p className="rtl:ml-2 font-bold text-black-300 text-lg leading-[22px]">
              ≤Xh
            </p>
            <span className="text-sm font-normal text-black-300">
              {t("myprofile.averageResponseTime")}
            </span>
          </div>
          <div className="flex items-center space-x-2 rtl:space-x-[unset mb-2">
            <p className="rtl:ml-2 font-bold text-black-300 text-lg leading-[22px]">
              X%
            </p>
            <span className="text-sm font-normal text-black-300">
              {t("myprofile.onTimeDeliveryRate")}
            </span>
          </div>
          <div className="flex items-center gap-2 mb-2">
            <p className="font-bold text-black-300 text-lg leading-[22px]">
              {/* US ${sellerdetails?.totalEarnings}+ */}
              <CustomCurrencyFormatter price={sellerdetails?.totalEarnings} />
            </p>
            <span className="text-sm font-normal text-black-300">
              {sellerdetails?.totalOrders} {t("seller.orders")}
            </span>
          </div>
        </div>
        <div className="flex flex-col space-y-3">
          {sellerdetails?.capabilities?.map((data, index) =>
            show == false
              ? index <= 4 &&
                data.status == 1 &&
                data?.data && (
                  <div key={index} className="flex items-center gap-2">
                    <img src="/img/verified.svg" alt="verified" />
                    <p className="text-sm font-bold text-black-300">
                      {data?.data}
                    </p>
                  </div>
                )
              : data.status == 1 &&
                data?.data && (
                  <div key={index} className="flex items-center gap-2">
                    <img src="/img/verified.svg" alt="verified" />
                    <p className="text-sm font-bold text-black-300">
                      {data?.data}
                    </p>
                  </div>
                )
          )}

          {sellerdetails?.capabilities?.length > 5 && (
            <a
              role="button"
              onClick={() => setShow(!show)}
              className="mt-1 text-sm font-normal cursor-pointer text-black-300 hover:underline"
            >
              {t("seller.seeCapabilities")} (
              {sellerdetails?.capabilities?.length})
            </a>
          )}
        </div>
      </div>
      <div>
        <div className="mx-auto mb-4 max-w-[400px] h-[400px] max-h-[400px]">
            <img
              // loader={imageloader}
              src={
                imageUrl +
                "?path=" +
                imageSlide?.filePath +
                "&name=" +
                imageSlide?.fileName +
                "&width=600&height=600"
              }
              alt={imageSlide?.fileName}
              onError={({ currentTarget }) => {
                currentTarget.onerror = null;
                currentTarget.src = "/img/no-image.png";
              }}
              className="m-auto w-auto h-full object-contain"
            />
          </div>
          <div className="flex flex-nowrap items-center space-x-[11px] pb-1 overflow-auto scroll-size">
            {sellerdetails?.vendorImages?.map((data, index) => (
              <a
                key={index}
                onClick={() => hadleChageImgeLoad(data)}
                className={`whitespace-nowrap cursor-pointer rtl:ml-[11px]  p-0.5 ${
                  imageSlide?.fileName == data?.fileName &&
                  "rounded border border-black"
                } aspect-square  hover:border-2 hover:border-black min-w-[96px] lg:w-full max-h-[96px] cursor-pointer relative border-2 rounded-lg overflow-hidden group`}
              >
                <div className="relative">
                  <img
                    src={
                      imageUrl +
                      "?path=" +
                      data?.filePath +
                      "&name=" +
                      data?.fileName +
                      "&width=600&height=600"
                    }
                    alt={data?.fileName}
                    onError={({ currentTarget }) => {
                      currentTarget.onerror = null;
                      currentTarget.src = "/img/no-image.png";
                    }}
                    className="object-contain w-full h-full m-auto"
                  />
                </div>
              </a>
            ))}
          </div>
      </div>
    </div>
  );
}
