"use client"
//React and Next
import { useRef, useState } from "react";
import { useTranslation } from "react-i18next";
//API
import { SellerDocumentDownloadApi } from "@/api/clientsideaction/getfilter";
//Components

export default function CertificationTab({ token, sellerdetails }) {
  const { t } = useTranslation();
  let scrl = useRef(null);
  const [scrollX, setscrollX] = useState(0);
  const [scrolEnd, setscrolEnd] = useState(true);
  
  const handleNav = (shift) => {
    scrl.current.scrollLeft += shift;

    setscrollX(scrollX + shift);

    if (
      Math.floor(scrl.current.scrollWidth - scrl.current.scrollLeft) <=
      scrl.current.offsetWidth
    ) {
      setscrolEnd(true);
    } else {
      setscrolEnd(false);
    }
  };

  const scrollCheck = () => {
    setscrollX(scrl.current.scrollLeft);
    if (
      Math.floor(scrl.current.scrollWidth - scrl.current.scrollLeft) <=
      scrl.current.offsetWidth
    ) {
      setscrolEnd(true);
    } else {
      setscrolEnd(false);
    }
  };

  const handleCopy = (text) => {
    navigator.clipboard
      .writeText(text)
      .then(() => {})
      .catch((err) => {
        console.error("Failed to copy text: ", err);
      });
  };

  const handleDocumentDownload = (data) => {
    SellerDocumentDownloadApi(token, data?.filePath, data?.fileName);
  };

  return (
    <div>
      <h3 className="mb-8 text-2xl font-bold text-black-300">
        {t("seller.certifications")}
      </h3>
      <div className="relative flex items-center overflow-auto">
        {scrollX !== 0 && (
          <div>
            <button
              onClick={() => handleNav(-200)}
              className="top-[39%] left-0 absolute flex justify-center items-center bg-trans-black w-6 h-[52px]"
            >
              <img src="/img/right-carosel.svg" alt="right-carosel" />
            </button>
          </div>
        )}
        <div
          className="flex space-x-4 overflow-hidden"
          ref={scrl}
          onScroll={scrollCheck}
        >
          {sellerdetails?.certificates?.map(
            (data, index) =>
              data.additionalInfo !== null && (
                <div
                  key={index}
                  className="p-2 group bg-lightblue min-w-40 whitespace-nowrap"
                >
                  {["pdf", "PDF"].includes(data?.fileName.split(".").pop()) && (
                    <img
                      src={"/img/pdf.svg"}
                      alt="pdf"
                      width={200}
                      height={180}
                      className="h-[180px] object-cover"
                    />
                  )}
                  {["doc", "DOC", "DOCS", "docs", "DOCX", "docx"].includes(
                    data?.fileName.split(".").pop()
                  ) && (
                    <img
                      src={"/img/word.svg"}
                      alt="word"
                      width={200}
                      height={180}
                      className="h-[180px] object-cover"
                    />
                  )}
                  {["xls", "XLS", "XLSX", "xlsx"].includes(
                    data?.fileName.split(".").pop()
                  ) && (
                    <img
                      src={"/img/excel.svg"}
                      alt="excel"
                      width={200}
                      height={180}
                      className="h-[180px] object-cover"
                    />
                  )}
                  {["png", "jpg", "jpeg", "JPG", "JPEG", "PNG"].includes(
                    data?.fileName.split(".").pop()
                  ) && (
                    <img
                      src={"/img/png.svg"}
                      alt="png"
                      width={200}
                      height={180}
                      className="h-[180px] object-cover"
                    />
                  )}
                  {["svg", "SVG"].includes(data?.fileName.split(".").pop()) && (
                    <img
                      src={"/img/svg.svg"}
                      alt="svg"
                      width={200}
                      height={180}
                      className="h-[180px] object-cover"
                    />
                  )}
                  <div className="flex flex-col space-y-[5px]">
                    <h5
                      onClick={(e) => handleDocumentDownload(data)}
                      className="mt-2 text-xs font-bold cursor-pointer text-black-300"
                      title="Click to download"
                    >
                      {data?.additionalInfo?.name}
                    </h5>
                    <p className="text-xs font-normal text-gray-900">
                      {data?.additionalInfo?.certificationType}
                    </p>
                    <a
                      role="button"
                      className="flex items-center space-x-2 rtl:space-x-[unset] cursor-pointer"
                    >
                      <span className="text-xs font-normal text-gray-900 rtl:ml-2 group-hover:text-blue-600 line-clamp-1">
                        {data?.additionalInfo?.refrenceNo}
                      </span>
                      <div
                        onClick={() =>
                          handleCopy(data?.additionalInfo?.refrenceNo)
                        }
                      >
                        <img
                          src="/img/copy.svg"
                          alt="copy"
                          className="group-hover:hidden"
                        />
                        <img
                          src="/img/copy-active.svg"
                          alt="copy-active"
                          className="hidden group-hover:block"
                        />
                      </div>
                    </a>
                  </div>
                </div>
              )
          )}
        </div>
        {sellerdetails?.certificates.length > 3 && (
          <div>
            <button
              onClick={() => handleNav(+200)}
              className="top-[39%] right-0 absolute flex justify-center items-center bg-trans-black w-6 h-[52px]"
            >
              <img
                src="/img/right-carosel.svg"
                alt="right-carosel"
                className="rotate-180"
              />
            </button>
          </div>
        )}
      </div>
    </div>
  );
}
